import { createSlice } from "@reduxjs/toolkit";
import firebase from "../../firebase";
const store = firebase.storage();

const slice = createSlice({
  name: "docs",
  initialState: {
    user: "x",
    error: null,
    files: [
      {
        name: "example_register.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2Fexample_register.pdf?alt=media&token=4fed4a2b-3aa3-40c9-910d-86754413020f",
        messages: [],
      },
      {
        name: "Leasehold-Information-Form-TA7-SLIF.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FLeasehold-Information-Form-TA7-SLIF.pdf?alt=media&token=46e234ab-1062-4182-847d-3054df404604",
      },
      {
        name: "eta10-specimen.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2Fta10-specimen.pdf?alt=media&token=03b4552f-0c2b-4148-9976-f36ad2b3d3d5",
      },
      {
        name: "TA6-specimen-may-2020.pdf",
        url:
          "https://firebasestorage.googleapis.com/v0/b/docs-conveyanceme/o/x%2FTA6-specimen-may-2020.pdf?alt=media&token=287a906f-74e8-4c35-bbb8-353a31333020",
      },
    ],
  },
  reducers: {
    uploadProgress: ({ files }, { payload }) => {
      const { name, progress } = payload;
      let exists = files.find((file) => file.name === name);
      exists
        ? Object.assign(exists, { progress })
        : files.push({
            name,
            progress,
          });
    },
    uploadURL: ({ files }, { payload }) => {
      const { name, url } = payload;
      let exists = files.find((file) => file.name === name);
      exists
        ? Object.assign(exists, { url })
        : files.push({
            name,
            url,
          });
    },
    uploadFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { uploadFail, uploadURL, uploadProgress } = slice.actions;
export const uploadFile = (file) => async (dispatch) => {
  let fileRef = store.ref("x/" + file.name);
  let uploader = fileRef.put(file);
  try {
    uploader.on(
      "state_changed",
      ({ ref, bytesTransferred, totalBytes }) => {
        const progress = Math.round((bytesTransferred / totalBytes) * 100);
        const name = ref.name;
        dispatch(uploadProgress({ name, progress }));
      },
      {},
      () =>
        fileRef
          .getDownloadURL()
          .then((url) => dispatch(uploadURL({ name: file.name, url })))
    );
  } catch (error) {
    dispatch(uploadFail(error));
  }
};
