import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Document } from "../../components/Document";
import Card from "../../components/Card";
import { showDoc } from "../dash/dashStore";
import { navTo } from "../nav/navStore";
import { Box, Button, Anchor, Text, DataTable } from "grommet";
import { Close, Previous, Next } from "grommet-icons";
import Uploader from "../docs/Uploader";

export const DashDoc = () => {
  const dispatch = useDispatch();
  const { showdoc } = useSelector((state) => state.dash);
  const [turnPage, setTurnPage] = useState(null);

  useEffect(() => {
    setTurnPage(null);
  }, [turnPage]);

  return (
    <Box align="center" pad={{ top: "large" }}>
      <Box direction="row" width="100%">
        <Button align="start" icon={<Previous />} onClick={() => setTurnPage(false)} />
        <Button icon={<Next />} onClick={() => setTurnPage(true)} />
        <Box flex/>
        <Button
          icon={<Close />}
          onClick={() => {
            dispatch(navTo({ main: "dashdocs", side: "" }));
            dispatch(showDoc(null));
          }}
        />
      </Box>
      <Box>
        <Document pdf={showdoc} turnPage={turnPage} height={1000} />
      </Box>
    </Box>
  );
};

export const DashDocs = () => {
  const dispatch = useDispatch();
  const { files } = useSelector((state) => state.docs);
  const [tempfiles, setTempFiles] = useState(null);
  const [select, onSelect] = useState(null);

  const columns = [
    {
      property: "name",
      primary: true,
      header: <Text>File</Text>,
      render: (datum) =>
        datum.name
          .replace("-", " ")
          .replace(/\.[^.]+$/, "")
          .substring(0, 18),
    },
    {
      property: "size",
      header: <Text>KB</Text>,
      render: (datum) => {
        Math.round(datum.size / 1024);

        // <Meter
        //   size="small"
        //   values={[
        //     { progress, color: progress === 100 ? "accent-1" : "black" },
        //   ]}
        // />
      },
    },
    {
      property: "lastModified",
      header: <Text>Date</Text>,
      render: (datum) =>
        new Date(datum.lastModified).toLocaleDateString("en-GB"),
    },
  ];

  return (
    <>
      {tempfiles ? (
        <DataTable
          columns={columns}
          data={tempfiles}
          step={10}
          select={select}
          onSelect={onSelect}
          onClickRow={(event) => dispatch(showDoc(event.datum))}
        />
      ) : null}

     <Box direction="row-responsive" gap="medium">
          <Uploader storeTempFiles={setTempFiles} />
          {files.map((file, i) => (
            <Anchor
              key={i}
              onClick={() => {
                dispatch(showDoc(file.url));
                dispatch(navTo({ main: "dashdoc", side: "" }));
              }}
              >
              <Card
                title=""
                subTitle=""
                children={<Document pdf={file} height={200} />}
                footer={file.name.replace(/-|_/g, " ").substring(0, 35)}
                size="small"
                />
            </Anchor>
          ))}
      </Box>
    </>
  );
};
